import React, { Component } from 'react'
import BoutonNav from '../../composants/boutonNav/BoutonNav'
import photoprofil from '../../ASSETS/photoprofil.jpg'
import './Apropos.css'

export default class Apropos extends Component {
    constructor(props){
        super(props);
        this.escFunction = this.escFunction.bind(this);
      }
      escFunction(event){
        if(event.keyCode === 27 && this.state.BoutonClick) {
            this.setState(state => ({
                BoutonClick: state.BoutonClick === false
              }));
        }
    }
    state = {
        BoutonClick: false
    }

    handleClick = () => {
        this.setState(state => ({
            BoutonClick: !state.BoutonClick
          }));
    }
    componentDidMount(){
        document.addEventListener("keydown", this.escFunction, false);
      }
      componentWillUnmount(){
        document.removeEventListener("keydown", this.escFunction, false);
      }
    render() {
        return (
            <div>
                <div>
                <div className="fond-accueil">
                    <h1 className={`titre-accueil${this.state.BoutonClick? ' blur': ''}`} > propos</h1>
                    <span className={`separator${this.state.BoutonClick? ' blur': ''}`}></span>
                    <BoutonNav Click={this.handleClick} Toggle={this.state.BoutonClick}/>
                </div>
                <div className={`section${this.state.BoutonClick? ' blur': ''}`}>
                    <img className='profil' src={photoprofil} alt='profil'/>
                    <p>    
Grand adepte des histoires et du dessins, j'tais gnralement le dessinateur du fond de la classe.
Adolescent, j'obtiens ma premire camra pour filmer des kickflips au skatepark. Je dmarre le cadrage et le montage.
Baccalaurat en poche, je quitte la classe prparatoire conomique aux grandes coles pour faire mes premiers court mtrage en autodidacte.
<br/>J'intgre plus tard une cole de cinma, directement admis en deuxime annes grce  mes acquis.
Nouveau matriel en main, nouvelles comptences, je commence les prestations vido rmunres.<br/><br/>

Aujourd'hui j'cris, ralise, cadre et/ou monte diffrents projets tout en gardant la mme passion qui me guidait au skatepark.
                    </p>
                </div>
            </div>
            </div>
        )
    }
}
